/* HTML:  */
.loader {
    --s: 15px;
    width: calc(var(--s)*2.33);
    aspect-ratio: 1;
    display: flex;
    justify-content: space-between;
    margin: 40px auto;
  }
  .loader::before,
  .loader::after {
    content: "";
    width: var(--s);
    --_g: no-repeat radial-gradient(farthest-side,#ffffff 94%,#ffffff00);
    background:
      var(--_g) top,
      var(--_g) bottom;
    background-size: 100% var(--s);
    transform-origin: 50% calc(100% - var(--s)/2);
    animation: l30 1s infinite;
  }
  .loader::after {
    transform-origin: 50% calc(var(--s)/2);
  }
  @keyframes l30 {
     70%,100% {transform:rotate(-270deg)}
  }